@import-nomalize;
// @import "xxx/aaa";
@import "helper.scss";

* {
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}
*::after {
  box-sizing: border-box;
}
*::before {
  box-sizing: border-box;
}
ul,
ol {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
body {
  font-family: $font-hei;
  font-size: 16px;
  line-height: 1.2;
  background: #f5f5f5;
}
.icon {
  width: 1em;
  height: 1em;
}
button,
input {
  font-family: inherit;
  font-size: inherit;
}
:focus {
  outline: none;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
.tagline {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
